.pad {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pad__label {
  margin-bottom: 0.2rem;
}

.drum-pad {
  color: #eee;
  border: none;
  box-shadow: inset 0 0 2px 0 rgba(255, 255, 255, 0.2),
    inset 0 0 3px 0 rgba(0, 0, 0, 0.6), inset 0 0 3px 5px rgba(0, 0, 0, 0.95);
  position: relative;
  width: 65px;
  height: 60px;
  background: #1b1a18;
  cursor: pointer;
  outline: none;
}
.drum-pad::before {
  content: "";
  position: absolute;
  left: 2px;
  right: 2px;
  height: 3px;
  top: 0;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background: rgba(255, 255, 255, 0.6);
  box-shadow: 0 1px 2px 0 rgba(255, 255, 255, 0.6);
}
.drum-pad:active,
.drum-pad.active {
  top: 0.2em;
  background: #121110;
  color: #aaa;
  box-shadow: inset 0 0 3px 5px rgb(0 0 0 / 45%);
}
.drum-pad:active::before,
.drum-pad.active::before {
  box-shadow: 0 1px 2px 0 rgb(255 255 255 / 30%);
  background: rgba(255, 255, 255, 0.3);
}