.controllers__on-off {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid #ee6d2d;
  border-radius: 8px;
  padding: 1rem;
}

.led {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.led--on {
  background-color: #f00;
  box-shadow: #000 0 -1px 7px 1px, inset #f00 0 -1px 9px, #f00 0 2px 12px;
}

.led--off {
  background-color: #940;
  box-shadow: #000 0 -1px 7px 1px, inset #600 0 -1px 9px, #f00 0 2px 12px;
}

.vl {
  border-left: 2px solid #ee6d2d;
  height: 60px;
}

.pad__label--bottom {
  margin-top: 8px;
}

.drum-pad--pressed {
  background: #121110;
  box-shadow: inset 0 0 3px 5px rgb(0 0 0 / 45%);
}
.drum-pad--pressed::before {
  box-shadow: 0 1px 2px 0 rgb(255 255 255 / 30%);
  background: rgba(255, 255, 255, 0.3);
}